import { Directive, EventEmitter, Input, Output, Component, ElementRef, TemplateRef, ViewChild, ContentChild, Host, ViewContainerRef, DoCheck } from "@angular/core";
import { TableComponent } from "./table.component";
import { TableFilter } from "./filter";
import { TableSort, TableSortFn } from "./sort";


export class ColumnComponent {


  
  public label: string = null;

  
  public field: string = null;


  /** sort */
  private _sort: TableSortFn | string = "string";

  
  public get sort(): TableSortFn {
    if (typeof this._sort === "string") {
      return TableSort[this._sort as any];
    }
    return this._sort;
  }

  public set sort(sort: TableSortFn) {
    if (this._sort === sort) return;
    this._sort = sort;
  }

  /** filter */
  private _filter: TableFilter | string = "string";

  
  public get filter(): TableFilter {
    if (typeof this._filter === "string") {
      return TableFilter[this._filter as any];
    }
    return this._filter;
  }

  public set filter(filter: TableFilter) {
    if (this._filter === filter) return;
    this._filter = filter;
  }

  public display: string;

  
  public format: string = null;

  
  public isFreeze: boolean = null;

  public height: number;
  public width: number;
  public left: number;
  public top: number;

  /** Th */
  
  public thTemplate: TemplateRef<any> = null;

  /** Td */
  
  public tdTemplate: TemplateRef<any> = null;

  
  column: any;

  //- 事件函數
  public constructor(
    public viewContainer: ViewContainerRef,
    public elementRef: ElementRef
  ) { }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: "ay-column",
  template: ""
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: ViewContainerRef, },
{type: ElementRef, },
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'label': [{ type: Input },],
'field': [{ type: Input },],
'sort': [{ type: Input },],
'filter': [{ type: Input },],
'format': [{ type: Input },],
'isFreeze': [{ type: Input, args: ["freeze", ] },],
'thTemplate': [{ type: ContentChild, args: ["thTemplate", ] },],
'tdTemplate': [{ type: ContentChild, args: ["tdTemplate", ] },],
'column': [{ type: Input },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
